/**
 * Component parent
 */

class Component {
  constructor({
    name,
    model,
  }) {
    this.name = name;
    this.model = this.proxyModel(model);
    this.reRender = null;
  }

  proxyModel(model) {
    return new Proxy(model, {
      set: (obj, prop, value) => {
        obj[prop] = value;
        if (this.reRender) this.reRender();
        return true;
      },
    });
  }
}

export default Component;
